.Navigation {
  position: fixed;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10; }

.Navigation-list {
  display: flex; }

.Navigation-item {
  position: relative;
  display: flex;
  margin-left: 6px; }

.Navigation-button {
  position: relative;
  display: block;
  width: 9px;
  height: 9px;
  padding: 0;
  border: 1px solid rgba(255, 255, 255, 0.6);
  border-radius: 8px;
  background: none;
  text-indent: -9999px;
  outline: none; }
  .Navigation-button:hover {
    background: white;
    border-color: white;
    cursor: pointer; }

.Navigation-button.is-selected {
  width: 13px;
  height: 13px;
  margin-top: -2px;
  background: white;
  border-color: white; }
  .Navigation-button.is-selected:hover {
    cursor: normal; }

.Navigation-list.is-dark .Navigation-item:after {
  background: rgba(102, 102, 102, 0.6); }

.Navigation-list.is-dark .Navigation-button {
  border-color: rgba(102, 102, 102, 0.6); }
  .Navigation-list.is-dark .Navigation-button:hover {
    background: #333;
    border-color: #333; }

.Navigation-list.is-dark .Navigation-button.is-selected {
  background: #333;
  border-color: #333; }
  .Navigation-list.is-dark .Navigation-button.is-selected:hover {
    cursor: normal; }

