.Slides {
  width: 100% !important;
  height: 100vh !important; }

.Slide {
  padding: 0 !important; }

.Slide-content {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  overflow-x: hidden; }

.Slide-contentItem {
  width: 100%; }

.Slide-icon {
  margin: 0 !important;
  border: none !important;
  background: none !important;
  box-shadow: none !important; }

.Slide-contentParagraph {
  max-width: 400px; }

